.main-button {
    &__text {
        font-family: 'Castoro';
    }

    &.light {
        --delay: .15s;
        --c-color-1: #{rgba(#ACA0FF, .15)};
        --c-color-2: #{rgba(#437dcf, .15)};
        --c-color-3: #{rgba(#fff, .5)};
        --c-color-4: #{rgba(#6550eb, .15)};
        --c-shadow: #{rgba(#004CB4, .1)};
        --c-shadow-inset-top: #{rgba(#fff, 1)};
        --c-shadow-inset-bottom: #{rgba(#fff, 1)};
        --c-radial-inner: #ECEFFC;
        --c-radial-outer: #fff;
        --c-color: #404660;
    }
   
}

.main-button {
    --duration: 7s;
    --delay: 0s;
    --easing: linear;
    --c-color-1: #{rgba(#5C86FF, .7)};
    --c-color-2: #275EFE;
    --c-color-3: #{rgba(#362A89, .4)};
    --c-color-4: #{rgba(#958ae0, .3)};
    --c-shadow: #{rgba(#2356ec, .5)};
    --c-shadow-inset-top: #{rgba(#2052e8, .8)};
    --c-shadow-inset-bottom: #{rgba(#5f95df, .6)};
    --c-radial-inner: #31267d;
    --c-radial-outer: #634fe8;
    --c-color: #fff;
    -webkit-tap-highlight-color: transparent;
    -webkit-appearance: none;
    outline: none;
    position: relative;
    cursor: pointer;
    border: none;
    display: table;
    border-radius: 24px;
    padding: 0;
    margin: 0;
    text-align: center;
    min-width: 140px;
    font-family: 'Inter';
    font-weight: 600;
    font-size: 16px;
    letter-spacing: .02em;
    line-height: 1.5;
    color: var(--c-color);
    background: radial-gradient(circle, var(--c-radial-inner), var(--c-radial-outer) 80%);
    box-shadow: 0 0 20px var(--c-shadow);
    margin: 0 20px;
    &:before {
        content: '';
        pointer-events: none;
        position: absolute;
        z-index: 3;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        border-radius: 24px;
        box-shadow: inset 0 4px 12px var(--c-shadow-inset-top), inset 0 -4px 6px var(--c-shadow-inset-bottom);
    }
    .wrapper {
        -webkit-mask-image: -webkit-radial-gradient(white, black);
        overflow: hidden;
        border-radius: 24px;
        min-width: 132px;
        padding: 12px 0;
        span {
            display: inline-block;
            position: relative;
            z-index: 1;
        }
        .circle {
            position: absolute;
            left: 0;
            top: 0;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            filter: blur(var(--blur, 8px));
            background: var(--background, transparent);
            transform: translate(var(--x, 0), var(--y, 0)) translateZ(0);
            animation: var(--animation, none) var(--duration) var(--easing) infinite var(--delay);
            &.circle-1,
            &.circle-9,
            &.circle-10 {
                --background: var(--c-color-4);
            }
            &.circle-3,
            &.circle-4 {
                --background: var(--c-color-2);
                --blur: 14px;
            }
            &.circle-5,
            &.circle-6 {
                --background: var(--c-color-3);
                --blur: 16px;
            }
            &.circle-2,
            &.circle-7,
            &.circle-8,
            &.circle-11,
            &.circle-12 {
                --background: var(--c-color-1);
                --blur: 12px;
            }
            &.circle-1 {
                --x: 0;
                --y: -40px;
                --animation: circle-1;
            }
            &.circle-2 {
                --x: 92px;
                --y: 8px;
                --animation: circle-2;
            }
            &.circle-3 {
                --x: -12px;
                --y: -12px;
                --animation: circle-3;
            }
            &.circle-4 {
                --x: 80px;
                --y: -12px;
                --animation: circle-4;
            }
            &.circle-5 {
                --x: 12px;
                --y: -4px;
                --animation: circle-5;
            }
            &.circle-6 {
                --x: 56px;
                --y: 16px;
                --animation: circle-6;
            }
            &.circle-7 {
                --x: 8px;
                --y: 28px;
                --animation: circle-7;
            }
            &.circle-8 {
                --x: 28px;
                --y: -4px;
                --animation: circle-8;
            }
            &.circle-9 {
                --x: 20px;
                --y: -12px;
                --animation: circle-9;
            }
            &.circle-10 {
                --x: 64px;
                --y: 16px;
                --animation: circle-10;
            }
            &.circle-11 {
                --x: 4px;
                --y: 4px;
                --animation: circle-11;
            }
            &.circle-12 {
                --blur: 14px;
                --x: 52px;
                --y: 4px;
                --animation: circle-12;
            }
        }
    }
}

@keyframes circle-1 {
    33% {
        transform: translate(0px, 16px) translateZ(0);
    }
    66% {
        transform: translate(12px, 64px) translateZ(0);
    }
}

@keyframes circle-2 {
    33% {
        transform: translate(80px, -10px) translateZ(0);
    }
    66% {
        transform: translate(72px, -48px) translateZ(0);
    }
}

@keyframes circle-3 {
    33% {
        transform: translate(20px, 12px) translateZ(0);
    }
    66% {
        transform: translate(12px, 4px) translateZ(0);
    }
}

@keyframes circle-4 {
    33% {
        transform: translate(76px, -12px) translateZ(0);
    }
    66% {
        transform: translate(112px, -8px) translateZ(0);
    }
}

@keyframes circle-5 {
    33% {
        transform: translate(84px, 28px) translateZ(0);
    }
    66% {
        transform: translate(40px, -32px) translateZ(0);
    }
}

@keyframes circle-6 {
    33% {
        transform: translate(28px, -16px) translateZ(0);
    }
    66% {
        transform: translate(76px, -56px) translateZ(0);
    }
}

@keyframes circle-7 {
    33% {
        transform: translate(8px, 28px) translateZ(0);
    }
    66% {
        transform: translate(20px, -60px) translateZ(0);
    }
}

@keyframes circle-8 {
    33% {
        transform: translate(32px, -4px) translateZ(0);
    }
    66% {
        transform: translate(56px, -20px) translateZ(0);
    }
}

@keyframes circle-9 {
    33% {
        transform: translate(20px, -12px) translateZ(0);
    }
    66% {
        transform: translate(80px, -8px) translateZ(0);
    }
}

@keyframes circle-10 {
    33% {
        transform: translate(68px, 20px) translateZ(0);
    }
    66% {
        transform: translate(100px, 28px) translateZ(0);
    }
}

@keyframes circle-11 {
    33% {
        transform: translate(4px, 4px) translateZ(0);
    }
    66% {
        transform: translate(68px, 20px) translateZ(0);
    }
}

@keyframes circle-12 {
    33% {
        transform: translate(56px, 0px) translateZ(0);
    }
    66% {
        transform: translate(60px, -32px) translateZ(0);
    }
}

html {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

* {
    box-sizing: inherit;
    &:before,
    &:after {
        box-sizing: inherit;
    }
}
