@use '../../styles/partials/variables' as *;
@use '../../styles/partials/typography' as *;


.nav {
    background-color: $primary-color-white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 2rem 5rem 0 5rem;
    align-items: center;
  

    &__logo {
        display: flex;
        font-size: 2rem;
        gap: 0.5rem;
        color: $primary-color-dark;
    }


    &__link {
        font-size: 2rem;
    }

    &__list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 30%;
        list-style: none;
        padding: 0;
        margin: 0;
        width: 40%;
    }
}

hr {
    background: linear-gradient($hr-gradient-angle, $hr-gradient-start-color 0%, $hr-gradient-mid-color 50%, $hr-gradient-end-color 100%);
    height: 1px;
    border: none;
    margin: 1rem 4rem 0 3rem;
  }
  